class Cookie {
	setCookie(name, val, day, pathname, domain) {
        let expireTime = '';
        let path = '';
        let domi = '';

        if (day) {
            const expires = +new Date() + day * 24 * 60 * 60 * 1000;
            expireTime = ';expires=' + (new Date(expires)).toGMTString();
        }

        if (pathname) {
            path = ';path=' + pathname;
        }

        if (domain) {
            domi = ';domain=' + domain;
        }
        
        document.cookie = name + '=' + val + expireTime + path + domi;
    }

    getCookie(name) {
    	const reg = new RegExp(name + '=(.+?)(?:;|$)', 'i');
    	const match = document.cookie.match(reg)

    	if (match) {
    		return unescape(match[1]);
    	}

    	return null;
    }
}

export default new Cookie()


