import Webp from 'commons/Webp'
import $ from 'commons/Dom'
import { render } from 'react-dom'
import React from 'react'

class RealUtil {
	constructor() {
		this.fontSize = null;
	}

	extend() {
        let options, name, src, copy, clone,
            target = arguments[ 0 ] || {},
            i = 1,
            length = arguments.length,
            deep = false;

        // Handle a deep copy situation
        if ( typeof target === "boolean" ) {
            deep = target;

            // Skip the boolean and the target
            target = arguments[ i ] || {};
            i++;
        }

        // Handle case when target is a string or something (possible in deep copy)
        if ( typeof target !== "object") {
            target = {};
        }

        // Extend this itself if only one argument is passed
        if ( i === length ) {
            target = this;
            i--;
        }

        for ( ; i < length; i++ ) {

            // Only deal with non-null/undefined values
            options = arguments[ i ]
            if ( options !== null && options !== undefined ) {

                // Extend the base object
                for ( name in options ) {
                    src = target[ name ];
                    copy = options[ name ];

                    // Prevent never-ending loop
                    if ( target === copy ) {
                        continue;
                    }

                    // Recurse if we're merging plain objects or arrays
                    if ( deep && copy && this.isPlainObject(copy)) {
                        if ( Array.isArray(copy) ) {
                            clone = src ? src : [];
                        } else {
                            clone = src ? src : {};
                        }

                        // Never move original objects, clone them
                        target[ name ] = this.extend( deep, clone, copy );

                    // Don't bring in undefined values
                    } else if ( copy !== undefined ) {
                        target[ name ] = copy;
                    }
                }
            }
        }

        // Return the modified object
        return target;
    }

    isPlainObject(obj) {
        //判断是否非window和DOM对象的对象，
        if ( !obj || obj.toString() !== "[object Object]" || obj.nodeType || obj.setInterval ) {
            return false;
        }

        //constructor是对创建对象的函数的引用（指针）。对于 Object 对象，该指针指向原始的 Object() 函数
        //判断obj是否具有isPrototypeOf属性，isPrototypeOf是挂在Object.prototype上的。通过字面量或自定义类（构造器）创建的对象都会继承该属性方法
        if ( obj.constructor && !obj.hasOwnProperty("constructor") && !obj.constructor.prototype.hasOwnProperty("isPrototypeOf") ) {
            return false;
        }

        var key;
        for ( key in obj ) {}

        return key === undefined || obj.hasOwnProperty(key);
    }

    getFontSize() {
        if (this.fontSize === null) {
            this.fontSize = parseInt(document.documentElement.style.fontSize, 10);
        }

        return this.fontSize;
    }

    getParams(obj) {
        const result = [];
        for (let key in obj) {
            result.push(key + '=' + obj[key]);
        }

        return result.join('&');
    }

    getParam(key) {
        return this.getHrefParam(key);
    }

    getHrefParam(key) {
        const reg = new RegExp(key + '=(.+?)(?:&|$|#)', 'i')
        const retArr = decodeURIComponent(location.href).match(reg);

        if (retArr) {
            return retArr[1];
        }

        return '';
    }

    getHashParam(key) {
        const reg = new RegExp(key + '?(.+?)(?:&|$|#)')
        const retArr = location.href.match(reg);

        if (retArr) {
            return true
        }

        return false;
    }

    replaceCdn(url) {
        return url.replace('image.tuhu.cn', 'img' + this.getHashCode(url) + '.tuhu.org').replace(/https?/, 'https');
    }

    supportWebp() {
        return Webp();
    }

    hideCar() {
        const loading = $('#loading');
        loading.hide ? loading.hide() : loading.style.display = 'none';

        const loadingBg = $('#loadingBg');
        loadingBg.hide ? loadingBg.hide() : loadingBg.style.display = 'none';
    }

    hashCode(str) {
        let hash = 0, i, chr, len;
        if (str.length === 0) {
            return hash;
        }

        for (i = 0, len = str.length; i < len; i++) {
            chr   = str.charCodeAt(i);
            hash  = ((hash << 5) - hash) + chr;
            hash |= 0; // Convert to 32bit integer
        }

        return hash;
    }

    getHashCode(url) {
        const hash = Math.abs(this.hashCode(url));

        return hash % 4 + 1;
    }

    getClientWidth() {
        return window.innerWidth;
    }

    getActivityUrl(url) {
        // return url.replace(Config.OLD_ACTIVITY_URL, Config.ACTIVITY_URL);
        return url;
    }

    toMoney(num) {
        num = num.toString().replace(/\$|\,/g, '');
        if (isNaN(num)) {
          num = 0;
        }

        let sign = num > 0;
        num = Math.floor(num * 100 + 0.50000000001);

        let cents = num % 100;
        num = Math.floor(num / 100).toString();

        if (cents < 10) {
          cents = '0' + cents;
        }

        for (let i = 0; i < Math.floor((num.length - (1 + i)) / 3); i++) {
          num = num.substring(0, num.length - (4 * i + 3)) + ',' + num.substring(num.length - (4 * i + 3));
        }

        return (sign ? '' : '-') + num + '.' + cents;
    }

    getHrefId() {
        return this.getHrefParam('id');;
    }

    /**f7 begin */
    getPageContainer() {
        const page = this.getParam('page') || 'home';
        const container = window.$('.page[data-page="' + page + '"]>.page-content').eq(-1);

        if (container.length > 0) {
            return container[0];
        }

        return null;
    }

    go(lastHistoryLength){
        const thisLength = window.$(window.myApp.mainView.container).find('.pages>.page').length;
        const goLength = lastHistoryLength - thisLength;
        if (goLength >= 0) {
            return;
        }
        
        const doms = window.$(window.myApp.mainView.container).find('.pages>.page');
        doms.each((index, item) => {
            if (index >= lastHistoryLength && index < thisLength - 1) {
                window.$(item).remove();
            }
        })

        history.go(goLength);
    }

    load(...args) {
        if (this._pageLoaded) {
            return;
        }

        this._pageLoaded = true;
        setTimeout(() => {
            this._pageLoaded = false;
        }, 100)
        
        window.myApp.mainView.router.load.apply(window.myApp.mainView.router, args);
    }

    loadReactPage(pageName, pageContainer) {
        window.config[pageName + '_instance'] = render(React.createElement(window.config[pageName]), pageContainer);
    }

    setFontSize() {
        const oHtml = document.documentElement;
        
        const screenWidth = window.innerWidth;
        if(screenWidth >= 640){
        oHtml.style.fontSize = '80px';
        } else if(screenWidth <= 320 ){
        oHtml.style.fontSize = '40px';
        } else {
        oHtml.style.fontSize = screenWidth / 320 * 40+'px';

        document.body.style.backgroundColor = '#fff';
        }
    }

    script(src) {
        return new Promise((resolve, reject) => {
            const script = document.createElement('script');
            script.src = src;
            script.onload = () => {
                resolve();
            }
            script.onerror = () => {
                reject();
            }

            document.body.appendChild(script);
        })
    }

    parseParams() {
        const params = {};
        let sep = '#!/';

        try {
            sep = window.myApp.params.pushStateSeparator;
        } catch(e) {}
        const url = decodeURIComponent(location.href).split(sep);

        if (url[1]) {
            url[1].split('&').forEach((item) => {
                if (item) {
                    const keyValue = item.split('=')
                    params[keyValue[0]] = keyValue[1];
                }
            })
        }

        return params;
    }
    /**f7 end */
}

export default new RealUtil()



