import myFetch from 'commons/MyFetch'
import Phone from 'commons/Phone'
import fetchJsonp from 'fetch-jsonp'
import RealUtil from 'commons/RealUtil'

const CALL_URL = '//wx.tuhu.cn/home/GetWxJsApiParams';
const WX_URL = 'https://res.tuhu.org/react/jweixin.min.js'
class TwiceShare {
	static share = (param) => {
		if (!Phone.isWX()) {
           return; 
        }

        const share = new TwiceShare(param);
        return share.config();
	}

	constructor(param) {
		this.param = param;

		this.sign = null;
	}

	getParam() {
        const param = {};

        param.Title = this.param.shareTitle;
        param.Description = this.param.shareDescrip;
        param.ImageUrl = this.param.shareImage;
        param.Link = this.param.shareUrl || location.href;
        
        return param;
    }

	config() {
		return this.getSign().then(() => {
			if (window.wx) {
				return this.wxConfig();
			} 

			return this.script(WX_URL).then(() => {
						return this.wxConfig();
					})	
		})
    }

	wxConfig() {
		return new Promise((resolve, reject) => {
			const param = this.getParam();

			window.wx.config({
				debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
				appId: this.sign.appid, // 必填，公众号的唯一标识
				timestamp: this.sign.timestamp, // 必填，生成签名的时间戳
				nonceStr: this.sign.nonceStr, // 必填，生成签名的随机串
				signature: this.sign.signature,// 必填，签名，见附录1
				jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
			});
			
			window.wx.ready(function () {
				window.wx.onMenuShareAppMessage({
					title: param.Title,
					desc: param.Description,
					link: param.Link.replace(/\{/g, '%7B').replace(/\}/g, '%7D'),
					imgUrl: param.ImageUrl,
					success: function () {
						resolve();
					},
					cancel: function () {

					}
				});

				window.wx.onMenuShareTimeline({
					title: param.Title,
					desc: param.Description,
					link: param.Link.replace(/\{/g, '%7B').replace(/\}/g, '%7D'),
					imgUrl: param.ImageUrl,
					success: function () {
						resolve();
					},
					cancel: function () {

					}
				});
			});
			
		})
	}

	getSign() {
		return fetchJsonp(CALL_URL + '?url=' + encodeURIComponent(location.href.split('#')[0]))
        .then(myFetch.checkStatus)
        .then((ret) => {
            this.sign = JSON.parse(ret);
        })
	}
}

export default TwiceShare


