import Phone from 'commons/Phone'
import Ta from 'commons/Ta'
import CallData from 'commons/CallData'
import * as Config from 'constants/Config'
import User from 'commons/User'

const LIMIT_TIME = 1000;
class MyFetch {
    static fetch = (file, opts = {}) => {
        const instance = new MyFetch();

        return instance.fetch(file, opts);
    }

    static setSession = (flag) => {
        const instance = new MyFetch();
        instance.setSession(flag);

        return instance;
    }

    static setShowStatus = (status) => {
        const instance = new MyFetch();
        instance.setShowStatus(status);

        return instance;
    }

    static setShowError = (error) => {
        const instance = new MyFetch();
        instance.setShowError(error);

        return instance;
    }

    static checkStatus = (res) => {
        const instance = new MyFetch();

        return instance.checkStatus(res);
    }

    static setCheckCode = (flag) => {
        const instance = new MyFetch();

        return instance.setCheckCode(flag);
    }

    constructor() {
        this.showStatus = true;

        this.showError = true;

        this.checkCode = false;

        this.session = false;
    }

    setSession(flag) {
        this.session = flag;
    }

    setShowStatus(showStatus) {
        this.showStatus = showStatus;

        return this;
    }

    setShowError(showError) {
        this.showError = showError;

        return this;
    }

    setCheckCode(flag) {
        this.checkCode = flag;

        return this
    }

	fetch(file, opts = {}) {
        if (location.href.indexOf('resource.html') > -1) {
            return;
        }
        
        opts.headers = opts.headers || {};

        if (this.showStatus) {
            CallData.getCallData().open(0);
        }

        if (process.env.NODE_ENV === 'development' && !process.env.NEW_FRAME) {
            file = 'http://' + process.env.NODE_IP + ':1337?url=' + encodeURIComponent(file);
        }

        if (process.env.NEW_FRAME) {
            file = file.replace(/.*?\/\/.+?\//, '/')
        }

        /*if (Phone.isApp()) {
            const ver = Phone.getAppVersion();

            if (Phone.isAndroid()) {
                opts.headers.VersionNumber = ver;
            } else {
                opts.headers['version iOS'] = ver;
            }
        } */

        let promise = null;
        if (this.session) {
            promise = User.getUserInfo().then((userInfo) => {
                if (userInfo) {
                    opts.headers = Object.assign({}, opts.headers, {
                        Authorization: 'Bearer ' + userInfo.usersession,
                        version: Phone.getAppVersion()
                    })
                }
            });
        } else {
            promise = Promise.resolve();
        }

        return  promise.then(() => {
                    const startTime = +new Date();

                    return fetch(file, opts)
                            .then((res) => {

                                /*if (res.status === 401) {
                                    if (Phone.isApp()) {
                                        Phone.relogin();
                                    }
                                }*/
                                if (this.showStatus) {
                                    CallData.getCallData().close(0);
                                }

                                return this.checkStatus(res);
                            })
                            .then((res) => {
                                if (this.checkCode) {
                                    return this.doCheckCode(res);
                                }

                                return res;
                            })
                            .then((res) => {
                                if (process.env.NODE_ENV !== 'development' && file.indexOf(Config.TA_BASE_URL) < 0) {
                                    const time = +new Date() - startTime;

                                    if (time > LIMIT_TIME) {
                                        const baseHost = location.protocol + '//' + location.host;
                                        Ta.ta({
                                            event_category: 'performance_monitor',
                                            event_action: 'SlowAPI',
                                            event_type: 'event',
                                            metadata: JSON.stringify({
                                                ms: time,
                                                url: baseHost + file.replace(baseHost, ''),
                                                params: opts.body
                                            })
                                        })
                                    }
                                }

                                if (res.Code !== undefined) {
                                    res.Code = res.Code - 0;
                                }

                                return res;
                            })
                            .catch((e) => {
                                if (this.showError) {
                                    CallData.getCallData().open(2);
                                }
                            });
                })
    }

    checkStatus(response) {
        if (response.ok) {
            return response.json()
        }

        const error = new Error(response.statusText)
        error.response = response
        throw error
    }

    doCheckCode(data) {
        if (data.code) {
            return data;
        }

        throw new Error(data.code)
    }
}

export default MyFetch



