import React, { Component } from 'react'
import { connect } from 'react-redux'

import Ta from 'commons/Ta'
import MyFetch from 'commons/MyFetch'
import RealUtil from 'commons/RealUtil'
import MyLink from 'commons/MyLink'
import TwiceShare from 'commons/TwiceShare'
// import DeepShare from 'commons/DeepShare'
// import DataContainer from 'commons/DataContainer'

import BackApp from '../components/BackApp'
import ShowCard from '../components/ShowCard'
import Md5 from '../lib/md5'

import hu from '../img/hu.png'

import * as Config from '../constants/Config'

class Home extends Component {
	constructor(props, context) {
		super(props, context)

		this.state = {
			data: {},
			flag: false
		}
	}

	componentWillMount() {
		const time = (Date.parse(new Date()) / 1000).toString();
		const random = Math.floor(Math.random()*10000).toString();
		const SALT = "lietome@2017!";
		const include = 'user,category';
		const sign = include + SALT  + time + random;
		let file = Config.FORUM_DATA_URL + (RealUtil.getHrefParam('id') || 76586) + '?include=user,category' + '&nonce=' + random + '&sign=' + Md5(sign) + '&timestamp=' + time;
		
		MyFetch.setShowStatus(false).setShowError(false).fetch(file)
		.then((data) => {
			if (data && data.code === 1) {
				this.setState({
					data: data.data
				})

				const shareData = data.data;
				const shareImg = shareData.body_original && shareData.body_original[0].type == "image" ? shareData.body_original[0].content : hu;
				const  shareDesc = (shareData.body_original && shareData.body_original[0].type == "image" && shareData.body_original[0].description) ? shareData.body_original[0].description : '此贴来自虎说论坛';
				const params = {
					'shareUrl': 'https://faxian.tuhu.cn/react/forumshare?id=' + (shareData.id || ''),
					'id': shareData.id || '',
					'shareImage': shareImg,
					'shareTitle': shareData.title || '',
					'shareDescrip': shareDesc,
					'URL': 'tuhu:/bbs/topic?id=' + (shareData.id || '')
				}
				
				TwiceShare.share(params);
				// DataContainer.setDataParams(params);
			} else {
				this.setState({
					flag: true
				})
			}

			return data
		}).then((data) =>  {
			if (data && data.code === 1) {
				MyLink.link('tuhu:/bbs/topic?id=' + (data.data.id || ''), ['a#backapp', 'a#backapp2'])
			}
		})
		// }).then(() => DeepShare.init(['a#backapp', 'a#backapp2']))
	}

	componentWillReceiveProps(nextProps) {
		
	}

	componentDidMount() {
		Ta.ta({'event_action': 'pageview', 'event_type': 'pv'})
	}

	render() {
		const datas = this.state.data

		if (this.state.flag) {
			return <div key="error">
						<img className="errorimg" src="https://res.tuhu.org/Activity/dist/img/error.jpg" width="100%" alt=""/>
						<span className="error">帖子不存在……</span>    
					</div>
		}

		if (!datas.body) {
			return null
		}
		
		return (
			<div className="page">
				<BackApp />
				<ShowCard  datas={datas}/>
			</div>
		)
	}
}

const mapStateToProps = (state, ownProps) => {
	return {
		// testProp: state.entities
	}
}

export default connect(mapStateToProps, {
	// testAction
})(Home)



