import React from 'react'
import 'css/callData.css'
import $ from 'commons/Dom'
import { render } from 'react-dom'

class CallData extends React.Component {
	/**启动实例
	CallData.getCallData();
	*/
	static getCallData = () => {
        if (!CallData.callData) {
            let dom = $('#calldata');
            if (!dom) {
                dom = document.createElement('div');
                dom.id = 'calldata';
                document.body.appendChild(dom);
            }
            CallData.callData = render(<CallData />, dom);
        }
        
        return CallData.callData; 
    }

	constructor(props, context) {
		super(props, context);

		this.status = ['数据请求中...', '', '网络异常']

		this.state = {
			setTime: 2000,
			display: 'none',
			status: 0,
			className: 'callData'
		}

		this.timeOuter = null;

		this.timerAnimation = null;
	}

	close() {
		if (this.timerAnimation) {
			clearTimeout(this.timerAnimation);
			this.timerAnimation = null;
		}

		this.setState({
			className: 'callData callDataHide'
		});

		this.timerAnimation = setTimeout(() => {
			this.setState({
				display: 'none'
			});
		}, 1000)
	}

	handleTimeout(){	
		if (this.state.setTime > 0) {
			if (this.timeOuter) {
				clearTimeout(this.timeOuter);
			}

			if (this.timerAnimation) {
				clearTimeout(this.timerAnimation)
			}

			this.timeOuter = setTimeout(() => {
								this.close(); 			
							}, this.state.setTime);
		}		
	}

	open(status){
		const state = {
			display: 'block',
			className: 'callData',
			status: status
		}

		this.setState(state);
		this.handleTimeout();		
	}

	render() {
		const style = {
			display: this.state.display
		};

		return (<div style={style}>
					<div className={this.state.className}>{this.status[this.state.status]}</div>
				</div>)
	}
}

export default CallData


