import 'normalize.css'
import './css/app.css'
import './css/style.css'

import React from 'react'
import { render } from 'react-dom'
import { hashHistory } from 'react-router'
import { syncHistoryWithStore } from 'react-router-redux'
import Root from './containers/Root'
import configureStore from './store/configureStore'

const store = configureStore()
const history = syncHistoryWithStore(hashHistory, store)

import Ta from 'commons/Ta'

function setFontSize(pathname = '/') {
  const oHtml = document.documentElement;
    
    const screenWidth = window.innerWidth;
    if(screenWidth >= 640){
      oHtml.style.fontSize = '80px';
    }else if(screenWidth <= 320 ){
      oHtml.style.fontSize = '40px';
    }else{
      if (
        ['allcars', 'mycars'].some((item) => {
          if (pathname.indexOf(item) > -1) {
            return true;
          }
        })
      ) {
        oHtml.style.fontSize = parseInt(screenWidth / 375 * 50)+'px';
      } else {
        oHtml.style.fontSize = screenWidth / 320 * 40+'px';
      }
    }
}

setFontSize();
hashHistory.listenBefore((params) => {
  setFontSize(params.pathname);
})

render(
  <Root store={store} history={history} />,
  document.getElementById('root')
)
Ta.loadTimeCount()



