import React, { Component } from 'react'

import src from '../img/app.jpg'

class BackApp extends Component {

    render() {
        return (<div className="load-tip">
                    <a id="backapp">
                        <img src={src} alt=""/>
                    </a>
                </div>)
    }
}

export default BackApp


