class DataContainer {
	constructor() {
		this.dataParams = null;
		this.appUrl = null;
        this.tireRatio = null;
        this.selectCar = null;
        this.loginWay = null;
        this.province = null;
	}

	setDataParams(data) {
        this.dataParams = data;
    }

    getDataParams() {
        return this.dataParams;
    }

    setAppUrl(data) {
        this.appUrl = data;
    }

    getAppUrl() {
        return this.appUrl;
    }

    setTireRatio(tireRatio) {
        this.tireRatio = tireRatio;
    }

    getTireRatio() {
        return this.tireRatio;
    }

    setSelectCar(selectCar) {
        this.selectCar = selectCar;
    }

    getSelectCar() {
        return this.selectCar;
    } 

    setLoginWay(loginWay) {
        this.loginWay = loginWay;
    } 

    getLoginWay() {
        return this.loginWay;
    } 

    setProvince(province) {
        this.province = province;
    }

    getProvince() {
        return this.province;
    } 
}

export default new DataContainer()


