/**
 * author: lishufen
 * fixTime: 2017-5-4
 * desc:  获取用户登录信息或者唤起登录页面
 * 调用方法: User.getUserInfo(isLogin).then((data) => {//获取登录信息后的逻辑})    
 * isLogin: 没有登录是否要去登录
 * 注意：如果页面要接糯米登录，在页面刚加载的时候要先设置登录方式为糯米登录 DataContainer.setLoginWay('nuomi')
 * updateBy: duwei
 * updateTime: 2017-7-6
 * updateDesc: 从后台取车型数据并保存，补充特殊参数时用来更新
 */

import * as Config from 'constants/Config'
import myFetch from 'commons/MyFetch'
import Phone from 'commons/Phone'
import Cookie from 'commons/Cookie'
import DataContainer from 'commons/DataContainer'
import fetchJsonp from 'fetch-jsonp'

class User {
	constructor() {
		this.userPromise = null;

		this.carPromise = null;
	}

	getUserInfo(gotoLogin) {
		const isApp = Phone.isApp();
		const loginWay = DataContainer.getLoginWay();
		
		if (!this.userPromise) {
			if (isApp) {
				this.userPromise = Phone.getUserInfo();
			} else {
				this.userPromise = 
					myFetch.setSession(false).fetch(Config.IS_LOGIN_URL, {
			            credentials: 'include'
			        })
			        .then((result) => {
			            if (result.result === 0) {
			                return null;
			            }

			            return myFetch.setSession(false).fetch(Config.USER_INFO_URL, {credentials: 'include'})
				                .then((data) => {
				                    if (data) {
				                        data.userid = data.UserID;
				                    }

				                    return data;
				                })
				                .catch((e) => {
				                	this.userPromise = null;
				                })
			        })
			        .catch((e) => {
		            	this.userPromise = null;
		            })
			}

		}

		return this.userPromise.then((data) => {
			if ((!data || !data.userid) && gotoLogin) {
				if (isApp) {
					window.callBackResponse = function() {
						if (Phone.isIOS()){
							location.reload()							
						}
						delete window.callBackResponse
						
					};
	            	if (Phone.isAndroid()) {
		                Phone.Android(window.callBackResponse);
		            } else {
		                Phone.IOS('window.callBackResponse');
		            }

		            throw new Error('go to login')
	            } else {
	            	if (loginWay === 'nuomi') {
	            		myFetch
						.setShowStatus(false)
	           			.setShowError(false)
						.fetch(Config.BASE_URL + Config.IS_NUOMI_CHANNEL, {
							credentials: 'include'
						}).then((data) => {
				        	if (data && (data.channel === 'nuomi百度糯米' || data.channel === 'nuomi糯米旺铺')) {
				        		this.getNuomiLoginInfo();
				        	} else {
				        		return Phone.link(Config.GET_OUT_URL + location.href)
				        	}
				        })
	            	} else {
	            		return Phone.link(Config.GET_OUT_URL + location.href)
	            	}					
	            }		
			}

			return data;	
		});
	}

	getNuomiLoginInfo() {
		myFetch.setSession(false).fetch(Config.BASE_URL + Config.GET_NUOMI_USERINFO, {
			credentials: 'include'
		})
        .then((result) => {
            if (result && !result.siteid) {
                location.href = Config.NUOMI_LOGIN_URL + result.appid +
						 '&tpUrl=' + encodeURIComponent(Config.NUOMI_RELOGIN_URL + 
						 	encodeURIComponent(location.href) + '&isredirect=1') + '&openAction=1&channelId=20029&dwz=1&needLogin=1';
            } else {
            	return myFetch.setSession(false).fetch(Config.USER_INFO_URL, {credentials: 'include'})
	                .then((data) => {
	                    if (data) {
	                        data.userid = data.UserID;
	                    }

	                    return data;
	                })
	                .catch((e) => {
	                	this.userPromise = null;
	                })
            }
        })
        .catch((e) => {
        	this.userPromise = null;
        })
	}

	getIOSDefaultCar() {
		return new Promise((resolve, reject) => {
			window.callback = (data) => {
				if (data) {
					// 存储原生车型信息
					// localStorage.setItem('_rawCar', JSON.stringify(data));
					
					const datas = {
						"Brand": data.Brand,
						"Vehicle": data.Vehicle,
						"VehicleID": data.ProductID,
						"TireSize": data.TireSize,
						"HubSize": "",
						"PaiLiang": data.Pailiang,
						"Nian": data.Nian,
						"SalesName": data.SalesName || "",
						"TID": data.TID || "",
						"LiYangID": data.LIYANGID || "",
						"VehicleLogin": data.ImageUrl,
						"PropertyList": data.PropertyList ? JSON.stringify(data.PropertyList) : ""
					}

					const cars = {
						"brand": data.Brand,
						"carID": data.CarID,
						"vehicle": data.Vehicle,
						"vehicleID": data.ProductID,
						"tiresize": data.TireSize,
						"hubsize": data.Hub || "",
						"pailiang": data.Pailiang,
						"nian": data.Nian,
						"salesname": data.SalesName || "",
						"tid": data.TID || "",
						"liyangID": data.LIYANGID || "",
						"brandImg": data.ImageUrl,
						"property": data.PropertyList || [],
						"carName": data.CarName,
						"TireSize": data.TireSize,
						"TireSizeForSingle": data.TireSizeForSingle
					}

					Cookie.setCookie('VehicleInfo', escape(JSON.stringify(datas)), 0, '/', 'tuhu.cn')

					data.TireSizeForSingle && localStorage.setItem('selectTire', data.TireSizeForSingle);

					resolve(cars);
				} else {
					resolve('');
				}
			}

			Phone.link(Config.DET_IOS_USER_CAR_INFO)
		})
	}

	getAndroidDefaultCar(level, isNeedCar = 1) {
        return new Promise((resolve, reject) => {
            window.carCallback = (result) => {
            	if (result) {
					// 存储原生车型信息
					// localStorage.setItem('_rawCar', result);

            		const data = JSON.parse(result)
	            	const datas = {
									"Brand": data.Brand,
									"Vehicle": data.VehicleName,
									"VehicleID": data.VehicleID,
									"TireSize": data.TireSize,
									"HubSize": data.Hub || "",
									"PaiLiang": data.PaiLiang,
									"Nian": data.Nian,
									"SalesName": data.SalesName || "",
									"TID": data.TID || "",
									"LiYangID": data.LiYangID,
									"VehicleLogin": data.VehicleLogin,
									"PropertyList": data.PropertyList || ""
								}

								const cars = {
									"brand": data.Brand,
									"vehicle": data.VehicleName,
									"vehicleID": data.VehicleID,
									"tiresize": data.TireSize,
									"hubsize": data.Hub || "",
									"pailiang": data.PaiLiang,
									"nian": data.Nian,
									"salesname": data.SalesName || "",
									"tid": data.TID || "",
									"liyangID": data.LiYangID,
									"brandImg": data.VehicleLogin,
									"property": eval(data.PropertyList),
									"carID": data.PKID,
									"carName": data.CarName,
									"TireSize": data.TireSize,
									"TireSizeForSingle": data.TireSizeForSingle
								}

					Cookie.setCookie('VehicleInfo', escape(JSON.stringify(datas)), 0, '/', 'tuhu.cn')
					data.TireSizeForSingle && localStorage.setItem('selectTire', data.TireSizeForSingle);
	                resolve(cars)
            	} else {
            		resolve('')
            	}
            }

            Phone.exeAndroid('getCar', {"carLevel" : level, "isNeedCar" : isNeedCar}, window.carCallback)
        })
    }

    _getIOSDefaultCar() {
		return new Promise((resolve, reject) => {
			window.callback = (data) => {
				if (data) {
					//data.TireSizeForSingle && localStorage.setItem('selectTire', data.TireSizeForSingle);
					localStorage.setItem('selectTire', data.TireSizeForSingle || '')
					resolve(data);
				} else {
					resolve('');
				}
			}

			Phone.link(Config.DET_IOS_USER_CAR_INFO)
		})
	}

	_getAndroidDefaultCar() {
        return new Promise((resolve, reject) => {
            window.carCallback = (result) => {
            	if (result) {
            		const data = JSON.parse(result);

					data.BrandType = data.CarBrand;
					data.SalesName = data.VehicleName;
					data.CarID = data.PKID;
					data.Pailiang = data.PaiLiang;
					data.Tires = data.TireSize;
					data.ProductID = data.VehicleID;
					data.Image = data.VehicleImage;
					data.ImageUrl = data.VehicleLogin;
					data.Vehicle = data.VehicleName;

					//data.TireSizeForSingle && localStorage.setItem('selectTire', data.TireSizeForSingle);
					localStorage.setItem('selectTire', data.TireSizeForSingle || '')
					
	                resolve(data)
            	} else {
            		resolve('')
            	}
            }

            Phone.exeAndroid('getCar', {"carLevel" : 0, "isNeedCar" : 0}, window.carCallback)
        })
    }
    
	getWebCar() {
		this.getUserInfo()
		.then((data) => {
			return fetchJsonp(Config.GET_USER_CAR_INFO + data.userid, {
			credentials: 'include',
			method: 'GET',
			mode: 'cors'
			})
			.then((res) => {
			if (res.ok) {
				return res.json()
			}

			const error = new Error(res.statusText)
			error.response = res
			throw error
			})
			.then((data) => {
			const carData = data.CarHistory && data.CarHistory[0];
			
			localStorage.setItem('_rawCar', JSON.stringify(carData));
			})
		})
	}

	getDefaultCar(reload, level = 4) {
		if (!this.carPromise || reload) {
			if (Phone.isTuhuIOS()) {
			  this.getWebCar();
			  return this.getIOSDefaultCar()
			}

			if (Phone.isTuhuAndroid()) {
			  this.getWebCar();
			  return this.getAndroidDefaultCar(level)
			}

			this.carPromise = 
			this.getUserInfo()
			.then((data) => {
			      return fetchJsonp(Config.GET_USER_CAR_INFO + data.userid, {
					credentials: 'include',
					method: 'GET',
			      	mode: 'cors'
			      })
			      .then((res) => {
		            if (res.ok) {
			            return res.json()
			        }

			        const error = new Error(res.statusText)
			        error.response = res
			        throw error
		          })
			      .then((data) => {
					const carData = data.CarHistory && data.CarHistory[0];
			      	localStorage.setItem('_rawCar', JSON.stringify(carData));

					return carData;
					// return data.CarHistory && data.CarHistory[0];
			      })
			      .catch((e) => {
                	this.carPromise = null;
                  })
		    })
		    .catch((e) => {
            	this.carPromise = null;
            });
		}

		return this.carPromise;
	}
}

export default new User()


