import base64 from 'base-64'
import Phone from 'commons/Phone'
import RealUtil from 'commons/RealUtil'

const MLINK_URL = 'https://res.tuhu.org/react/lib/mlink.min.js';
class MyLink {
	static link = (url, selector) => {
		if (!Phone.isWX()) {
           return; 
        }

        const link = new MyLink(url, selector);
        return link.mlink();
	}

	constructor(url, selector) {
		this.url = url;
		this.selector = selector;
	}

	mlink() {
        const results = [];
        for(let i = 0; i < this.selector.length; i++) {
       		const data = base64.encode(JSON.stringify({
            	url: '/webView?url=' + encodeURIComponent(this.url)
        	}))

            results.push({
                mlink: 'https://a.mlinks.cc/AakR',
                button: typeof(this.selector[i]) === 'string' ? document.querySelector(this.selector[i]) : this.selector[i],
                params: {
                	data: data
                }
            })
        }

        if (window.Mlink) {
            new window.Mlink(results);
        } else {
            RealUtil.script(MLINK_URL).then(() => {
                new window.Mlink(results);
            });
        }  
    }
}

export default MyLink


