import React from 'react'
import $ from 'commons/Dom'
import { render } from 'react-dom'

class TipBox extends React.Component {
	static getTipBox = () => {
		if (!TipBox.instance) {
			let dom = $('#tipbox');
	        if (!dom) {
	            dom = document.createElement('div');
	            dom.id = 'tipbox';
	            document.body.appendChild(dom);
	        }

	        TipBox.instance = render(<TipBox />, dom);
		}

		return TipBox.instance;
	}

	constructor(props, context) {
		super(props, context);

		this.state = {
			type: 'tip',
			msg: '',
			hasBtn: false,
			display: 'none'
		}

		this.timeOuter = null;

		this.setTime = 2000

		this.callback = null;
	}

	handleClose() {
		if (this.callback) {
			return this.callback()
		}

		if (this.timeOuter) {
			clearTimeout(this.timeOuter);
			this.timeOuter = null;
		}

		this.setState({
			display: 'none'
		});
	}

	handleTimeout(){	
		if (this.setTime > 0) {						
			this.timeOuter = setTimeout(() => {
								this.handleClose(); 			
							}, this.setTime);
		}		
	}

	handleOpen(type, msg, hasbtn, {setTime, callback} = {}){
		const state = {
			display: 'block',
			type: type,
			msg: msg,
			hasBtn: hasbtn
		}

		this.setTime = setTime === undefined ? this.setTime : setTime;
		this.callback = callback;
		this.setState(state);
		this.handleTimeout()
	}

	render() {
		const style = {
			display: this.state.display
		};

		let btn = '';
		let iconBox = '';
		let boxStyle = '';

		if (this.state.type === 'success') {
			boxStyle = 'success'
			iconBox = <div className="icon_box">           
									<div className="line_short"></div>           
									<div className="line_long"></div>       
								</div>
		}else if(this.state.type === 'error'){
			boxStyle = 'lose'	
			iconBox = <div className="icon_box">           
									<div className="line_left"></div>           
									<div className="line_right"></div>       
								</div>
		} else {
			boxStyle = 'tip'
			iconBox = '!'
		}

		if(this.state.hasBtn && (this.state.type === 'error' || this.state.type === 'tip')){ 
			btn = <button className="okoButton redOkoButton" onClick={this.handleClose.bind(this)}>ok</button>;
		} else if(this.state.hasBtn && this.state.type === 'success'){
			btn = <button className="okoButton" onClick={this.handleClose.bind(this)}>ok</button>;
		}

		return (
			<div>
				<div onClick={this.handleClose.bind(this)} className="bgpopUp" style={style}></div>
				<div style={style} className="animationTipBox">
					<div className={boxStyle}>   
						<div className="icon">       
							{iconBox}  
						</div>
						<div className="dec_txt">{this.state.msg}</div>
					</div>
					{btn}
				</div>
			</div>

		)
	}
}

export default TipBox


