import React, {Component} from 'react'

import LoadImg from 'commons/LoadImg'

import MyUtil from '../commons/MyUtil'

class ItemReply extends Component {
    render() {
        const {item, items, index} = this.props;
        const userReply = item.user.data;
        const replyData = items;

        return(<div className="commentCon">
                    <div className="reviewer">
                        <img src={userReply.avatar}/>
                    </div>
                    <div className="commentConR">
                        <div className="commentConTop">
                            <div className="reviewerName">{userReply.name}</div>
                            <div className={items.hotdata ? "xiaoshi" : "commentFloor"}>{index+1}楼</div>
                        </div>
                        <div className={item.source_id !== 0 ?  "original" : "xiaoshi"}>
                            {replyData.data.map((res, inde) => {
                                const userReplys = res.user.data

                                if (item.source_id === res.id) {
                                    return <div className="originalComment" key={inde}>
                                                <div className="originalTop">
                                                    <div className="originalName">{userReply.name}</div>
                                                    <div className="originalTime">{MyUtil.showTime(res)}</div>
                                                    <div className={res.vehicle_type ? '' : 'xiaoshi'}>
                                                        <div>发表在</div>
                                                        <div className="originalFloor">{res.vehicle_type}</div>
                                                    </div>
                                                </div>
                                                <div className="originalBlock">
                                                    <p>{res.topic_title}</p>
                                                </div>
                                            </div>
                                }
                            })}
                        </div>
                        <div className="commentBlock">
                            <p>{item.body_original}</p>
                            <p className={(item.image_urls.length > 0) ? '' : 'hide'}>
                                {item.image_urls.map((res, index) => {
                                    return <LoadImg key={index} src={res} width={window.innerWidth * 0.808}/>
                                })}
                            </p>
                        </div>
                        <div className="commentTime">{item.created_at}</div>
                    </div>
                </div>)
    }
}

export default ItemReply


