import RealUtil from 'commons/RealUtil'
import * as Config from 'constants/Config'
import myFetch from 'commons/MyFetch'
import User from 'commons/User'
import Phone from 'commons/Phone'

class Ta {
    static ta = (obj) => {
        Ta.getTrack(obj);
    }

    static getTrack = (obj) => {
        try {
            const instance = new Ta();
        
            instance.getTrack(obj);
        } catch(e) {

        }
    }

    static loadTimeCount = () => {
       const interval = setInterval(() => {
            if(window.pageLoadTime) {
                clearInterval(interval);

                Ta.ta({
                    event_category: 'performance_monitor',
                    event_action: 'LoadTime_home', 
                    event_type: 'event', 
                    metadata: JSON.stringify({
                       ms: parseInt(window.pageLoadTime, 10),
                       url: location.href
                    })
                })
            }
     }, 300)
    }

    constructor(){
        this.devices = [
              'other iphone',
              'Apple iPhone 6 Plus / 6s Plus',
              'Apple iPhone 6 / 6s',
              'Apple iPhone 4, 4s, 5, 5c, 5s',
              'android device'
          ];
        this.appNames = ['tuhuAndroid', 'tuhuIOS', ''];
        this.osNames = ['Android', 'iOS', 'other OS']
        this.WXSid = 'WXSid';
        this.WXUid = 'WXUid';
        this.userid = '';
        this.deviceid = '';
        this.paramObj = {};
    }

    getTrack(obj) {
        const domain = location.host;
        const promises = [];
        this.paramObj = obj;
        
        if (this.isFirstInTa()) {
            obj.event_action = 'get_info';
            obj.event_type = 'get_info';
        }
        
        if (Phone.isApp()) {
            promises.push(
              Phone.getDeviceID().then((res)=>{
                if (res) {
                  this.deviceid =res;
                }
              })
            )
        }

        if (Phone.isApp() || domain === 'wx.tuhu.cn' || domain === 'wx.tuhu.com') {
            promises.push(
                User.getUserInfo().then((data)=>{
                  if (data) {
                    this.userid = data.userid;
                  }
                })
              )
        } else {
             this.userid = RealUtil.getHrefParam('utm_content') || '';
        }
        
      Promise.all(promises)
      .then(()=>{
        this.ta();
      })
    }
    
    isFirstInTa() {
        return !localStorage.getItem('tuhuTA');
    }

    getDeviceInfo() {
        if (Phone.isTuhuIOS()) {
            return new Promise((resolve, reject)=>{
                window.userInfoCallback = (res)=>{
                    resolve(res);
                }

                // location.href = 'tuhuaction://getDeviceInfo#userInfoCallback';
                window.webkit.messageHandlers.getDeviceInfo.postMessage({"callback": "userInfoCallback"})
            })            
        } else if (Phone.isTuhuAndroid()) {
            return new Promise((resolve, reject)=>{
                window.callback = (res)=>{
                    const info = JSON.parse(JSON.parse(res).track);
                    resolve(info)
                }

                Phone.Android(window.callback, 'getDeviceID')
            })
        }
    }

    handleAppInfo(data, info) {
        return JSON.stringify(data) + info;
    }

    handleTaData(data) {
        data.client_time = Math.floor(+new Date() / 1000)

        if (document.referrer) {
            data.refer = document.referrer.substr(0, 512);    
        };

        data.url = window.location.href.substr(0, 512);

        if(data.event_action) {
            data.event_action = data.event_action.substr(0, 512);
        }

        if(data.event_category) {
            data.event_category = data.event_category.substr(0, 32);
        }

        data.sid = sessionStorage.getItem('WXSid') || '';

        data.uuid = localStorage.getItem('WXUid') || '';

        if (this.isFirstInTa()) {
            if (Phone.isApp()) {
                this.getDeviceInfo()
                .then((info)=>{
                    if (info) {
                        data.app_version = this.appVersion();
                        data.app_name = this.appName();
                        data.is_gps_enabled = info.is_gps_enabled;
                        data.is_notify_enabled = info.is_notify_enabled;
                        data.screen_resolution = info.screen_resolution;
                        data.os_name = info.os_name;
                        data.os_version = info.os_version;
                        data.device_carrier = info.device_carrier;
                        data.device_model = info.device_model;
                        data.lang = info.lang;
                        data.country = info.country;
                        data.city = info.city;
                        data.location = info.location;
                    }                
                })
            } else {
                data.app_version = this.appVersion();
                data.app_name = this.appName();
                data.is_gps_enabled = '';
                data.is_notify_enabled = '';
                data.screen_resolution = this.screenResolution();
                data.os_name = this.osName();
                data.os_version = this.osVersion();
                data.device_carrier = '';
                data.device_model = this.deviceModel();
                data.lang = this.lang();
                data.country = this.country();
                data.city = '';
                data.location = '';
            }

            localStorage.setItem('tuhuTA', '8868');
        }

        return data;
    }

    appVersion() {
        if (Phone.isTuhuAndroid()) {
            return 'Android' + window.isVersion;
        } else if (Phone.isTuhuIOS()) {
            return window.isVersion;
        } else {
           return '' 
        }
    }

    appName() {
        if (Phone.isTuhuAndroid()) {
            return this.appNames[0];
        } else if (Phone.isTuhuIOS()){
            return this.appNames[1];
        } else {
            return this.appNames[2];
        }
    }

    lang() {
        return navigator.language.split('-')[0]
    }

    country(){
        return navigator.language.split('-')[1]
    }

    screenResolution() {
        return window.innerWidth + 'x' + window.innerHeight;
    }

    osName() {
        if (Phone.isAndroid()) {
            return this.osNames[0]
        } else if (Phone.isIOS()){
            return this.osNames[1]
        }

        return this.osNames[2]
    }

    osVersion() {
        if (Phone.isAndroid()) {
            return navigator.userAgent.match(/Android\s\d\.\d/)[0].split(' ')[1]
        } else if (Phone.isIOS()) {
            const match = navigator.userAgent.match(/\d+_\d+(?:_\d+)?/)
            if (match) {
                return match[0].replace(/_/g,'.')
            } 
        }

        return ''
    }

    appID() {
        if (Phone.isTuhuAndroid()) {
            return 'h5_android_app';
        } else if (Phone.isTuhuIOS()) {
            return 'h5_ios_app';
        } else if (Phone.isWX()) {
            return 'weixin';
        } else if (Phone.isPc()) {
            return 'tuhu_web';
        }

        return 'tuhu_wap';
    }

    deviceModel() {
        if (Phone.isIOS()) {
            switch (window.innerWidth){
                case 414:
                    return this.devices[1]

                case 375:
                    return this.devices[2]

                case 320:
                    return this.devices[3]

                default:
                    return this.devices[0]
            }
        }

        return this.devices[4]
    }

    childurl() {
        return location.href.substr(location.origin.length + 1);
    }

    goFetch(data) {
        let url = '';

        url = data.event_type === 'pv' ? Config.TA_STATIC_URL_WPV : Config.TA_STATIC_URL_WEVENT;
        // if (Phone.isWX()) {
        //     url = (data.event_type == 'pv') ? Config.TA_STATIC_URL_WPV : Config.TA_STATIC_URL_WEVENT;
        // } else {
        //     url = (data.event_type == 'pv') ? Config.TA_STATIC_URL_PV : Config.TA_STATIC_URL_EVENT;
        // }

        myFetch
           .setShowStatus(false)
           .setShowError(false)
           .fetch(url, {
                method:'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json;charset=UTF-8'
                  },
                body: JSON.stringify(this.handleTaData(data))
                // credentials: 'include'
        })
        .then((res)=>{
            if (res) {
                // if (Phone.isWX()) {
                    sessionStorage.setItem(this.WXSid, res.sid || '');
                    localStorage.setItem(this.WXUid, res.uuid || '');
                // };
            };
        }) 
    }

    ta() {
        Phone.getNetWork().then((network) => {
            const baseData = {
                app_id : this.appID(),
                event_category : this.childurl(),
                user_id : this.userid,
                device_id : this.deviceid,
                level : 'info',
                network: network
            }

            const data = Object.assign({}, baseData, this.paramObj)

            this.goFetch(data);
        });
    }
}

export default Ta



